body {
  margin: 0;
}
.additional-products {
  margin-top: 28px;
}
.additional-products__title {
  position: relative;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  text-align: left;
  color: #111111;
  background-color: #ffffff;
  width: 247px;
  margin: 0;
  margin-bottom: 34px;
  margin-left: -1px;
}
.additional-products__title::after {
  position: absolute;
  content: "";
  width: 54px;
  height: 3px;
  top: 14px;
  right: -54px;
  background-color: #68b738;
}
.additional-products__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.additional-products__list-item {
  box-sizing: border-box;
  border-bottom: 1px solid #d9d9d9;
}
.additional-products__list-item:first-child {
  box-sizing: border-box;
  border-top: 1px solid #d9d9d9;
}
.additional-products__title-item {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  text-transform: uppercase;
  color: #222222;
  margin: 0;
  margin-top: 13px;
  margin-bottom: 11px;
}
.additional-products__table {
  border-spacing: 0;
  width: 282px;
  margin-bottom: 15px;
}
.additional-products__cell {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 14px;
  color: #444444;
  margin: 0;
}
.additional-products__cell--price {
  text-align: right;
}
.additional-products__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 280px;
  height: 40px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
  margin-bottom: 17px;
}
.additional-products__action {
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  position: relative;
  width: 280px;
  height: 289px;
  margin-top: 39px;
  margin-bottom: 35px;
}
@media (min-resolution: 1.5dppx) {
  .additional-products__action {
    background-image: url("/img/gift-photo-mobile@2x.jpg");
    background-size: 280px 289px;
  }
}
.additional-products__condition {
  display: inline-block;
  width: 170px;
  position: relative;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #ffffff;
  text-align: center;
  margin-left: 55px;
  margin-top: 187px;
}
.additional-products__condition::before {
  position: absolute;
  content: "";
  width: 55px;
  height: 60px;
  top: -122px;
  left: 58px;
  background-image: url("/img/icon-gift.svg");
}
